.home {
  background-image: linear-gradient(
      0deg,
      hsl(218, 26%, 14%, 0.8),
      hsl(218, 26%, 14%, 0.8)
    ),
    url('../../../assets/Social-1-20.jpg');
  height: 100vh;
  background-size: cover;
}

.home .container {
  display: grid;
  grid-template-columns: 40% 40%;
  gap: 20%;
}
.home-left {
  margin-top: 200px;
  margin-left: 100px;
}

.home-left .top .hi {
  font-size: 100px;
  background: var(--color-orange);
  padding: 30px 44px;
  color: white;
  font-weight: bold;
  text-align: center;
}
.home-left .top .five {
  font-size: 100px;
  background: white;
  font-weight: bold;
  color: black;
  padding: 30px 55px;
}

.home-left .bottom {
  margin-top: 60px;
}
.home-left .bottom .de {
  font-size: 100px;
  background: hsl(218, 26%, 14%, 0.5);
  color: white;
  padding: 30px 49px;
  text-align: center;

  font-weight: bold;
}

.home-left .bottom .ee {
  font-size: 80px;
  background: var(--color-yellow);
  color: white;
  padding: 50px 40px 32px;
  font-weight: bold;
  text-align: center;
}

.home-left-text {
  margin-top: 40px;
  margin-left: 10px;
  letter-spacing: 0.4px;

  color: white;
  font-weight: bold;
}

/* home right */

.home-right {
  margin-top: 200px;
  color: white;
  line-height: 1;
}

.home-right h1 {
  font-size: 80px;
  /* margin-left: 180px; */
  margin: 0 auto;
}
.home-right h1 span {
  color: var(--color-orange);
}

.home-right h3 {
  font-size: 80px;
  font-weight: 100;
}

.name2 {
  margin-left: 0px;
}

@media screen and (max-width: 600px) {
  .home .container {
    display: flex;
    flex-wrap: wrap;
  }
  .home {
    margin-bottom: 200px;
    height: fit-content;
  }
  .home-right {
    margin: 50px auto;
  }
  .home-right h1 {
    margin: 0 auto;
    text-align: center;
  }
  .home-right h3 {
    margin: 0 auto;
    text-align: center;
  }
  .home-left {
    margin: 50px auto;
  }
  .home-left-text {
    letter-spacing: 0.4px;
  }
}

@media screen and (max-width: 1024px) {
  .home .container {
    grid-template-columns: 1fr 1fr;
    width: 100%;
    gap: 2rem;
  }
}

